.create-button {
  padding: 9px 23px;
  font-size: 16px;
  background-color: #4a9700;
  color: #fff;
  border: outset #3e7e00;
  cursor: pointer;
}

.create-button:hover {
  background-color: #3e7e00;
  border: inset #4a9700;
}